import { useState } from "react";
import { Controller, useForm } from "react-hook-form";
import { useParams } from "react-router";
import DatePicker from "react-datepicker";
import { cn } from "../../utils/custom_functions";
import moment from "moment";
import { useMutation, useQuery, useQueryClient } from "react-query";
import { FLOORS_ARRAY, HELPERS_ARRAY } from "../../utils/information";
import { ErrorAlert, SuccessAlert } from "./alerts/alerts";
import { Button } from "./custom_button";
import { fetchBookingFunction, updateBookingFunction } from "../../api/api";
import { LoadingComponent } from "./loading-content";
import { ThreeDots } from "react-loader-spinner";

export function UpdateAdditionalINformationCard() {
  const { id } = useParams();
  const [disableUserInterface, setDisableUserInterface] = useState(false);
  const queryClient = useQueryClient();
  const { register, handleSubmit, control, setValue } = useForm({
    defaultValues: {
      booking_time: "15:30",
    },
  });

  const {
    data: booking,
    error: bookingErrors,
    isError: isBookingError,
    isLoading: isBookingLoading,
  } = useQuery({
    queryKey: ["fetch-additional-information"],
    queryFn: () => fetchBookingFunction(id),
    onSuccess: (data) => {
      setValue("booking_date", data?.booking_date);
      setValue("booking_time", data?.booking_time);
      setValue("helpers", data?.helpers);
      setValue("floors", data?.floors);
      setValue("payment_option", data?.payment_option);
      setValue("customer_driver_note", data?.customer_driver_note);
    },
  });

  const {
    isLoading: isUpdateLoading,
    isSuccess: isUpdateSucces,
    mutateAsync: updateBookingAsync,
    isError: isUpdateError,
    error: updateError,
  } = useMutation("update-additional-information", {
    mutationFn: updateBookingFunction,
    onSuccess: (event) => {
      setDisableUserInterface((s) => false);
      queryClient.invalidateQueries(["fetched-booking"]);
    },
    onMutate: (event) => {
      setDisableUserInterface((l) => true);
    },
    onError: (event) => {
      setDisableUserInterface((e) => false);
    },
  });

  const onSubmit = async (data) => {
    try {
      await updateBookingAsync({ id: booking.id, ...data });
    } catch (error) {}
  };

  if (isBookingLoading) {
    return <LoadingComponent />;
  }
  if (isBookingError) {
    return <ErrorAlert secondaryText={`${bookingErrors.response.data}`} />;
  }

  return (
    <div className="px-2 w-full">
      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="custom_datepicker_width my-4 flex flex-col">
          <Controller
            control={control}
            name="booking_date"
            render={({ field }) => (
              <DatePicker
                className={cn(
                  "text-sm w-full rounded-lg text-gray-700 border-gray-200 focus:ring-yellow-400 focus:bg-white focus:border-yellow-400 py-3 px-4 disabled:bg-gray-200 disabled:pointer-events-none disabled:opacity-70"
                  //   {
                  //     "border-red-500 bg-red-50 bg-opacity-100": isDateTimeError,
                  //   }
                )}
                disabled={disableUserInterface}
                dateFormat="yyyy-MMM-dd"
                timeFormat="HH:mm"
                minDate={Date.now()}
                showPopperArrow={false}
                closeOnScroll={true}
                selected={field.value}
                onChange={(date) =>
                  field.onChange(moment(date).format("yyyy-MM-DD"))
                }
                onFocus={(e) => (e.target.readOnly = true)}
              />
            )}
          />
          <span className="text-xs text-gray-400 mt-2">Booking date</span>
        </div>
        <div className="custom_datepicker_width my-4 flex flex-col">
          <Controller
            control={control}
            name="booking_time"
            render={({ field }) => (
              <DatePicker
                timeFormat="HH:mm"
                dateFormat="HH:mm"
                timeCaption="Time"
                disabled={disableUserInterface}
                className={cn(
                  "text-sm rounded-lg w-full  disabled:bg-gray-200 disabled:pointer-events-none disabled:opacity-70 text-gray-700 border-gray-200 focus:ring-yellow-400 focus:bg-white focus:border-yellow-400 py-3 px-4",
                  {
                    // "border-red-500 bg-red-50 bg-opacity-100": isDateTimeError,
                  }
                )}
                minDate={Date.now()}
                timeIntervals={15}
                showTimeSelect={true}
                showTimeSelectOnly={true}
                showPopperArrow={false}
                selected={new Date(Date.parse(`2024-06-05 ${field.value}`))}
                onChange={(time) =>
                  field.onChange(moment(time).format("HH:mm:ss"))
                }
                onFocus={(e) => (e.target.readOnly = true)}
              />
            )}
          />
          <span className="text-xs text-gray-400">Booking time</span>
        </div>
        <div>
          <select
            {...register("helpers", {
              valueAsNumber: true,
            })}
            id="helpers"
            name="helpers"
            disabled={disableUserInterface}
            className="block text-sm appearance-none w-full border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded-lg leading-tight focus:outline-none focus:ring-yellow-400 focus:bg-white focus:border-yellow-400  disabled:bg-gray-200 disabled:pointer-events-none disabled:opacity-70"
          >
            {HELPERS_ARRAY.map((_, i) => (
              <option key={i + 1} value={i + 1}>
                {i + 1} Helpers
              </option>
            ))}
          </select>
          <span className="text-xs text-gray-400">
            Request additional suport
          </span>
        </div>
        <div>
          <select
            {...register("floors", {
              valueAsNumber: true,
            })}
            id="floors"
            name="floors"
            disabled={disableUserInterface}
            className="mt-4 block text-sm appearance-none w-full  border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded-lg leading-tight focus:outline-none focus:ring-yellow-400 focus:bg-white focus:border-yellow-400  disabled:bg-gray-200 disabled:pointer-events-none disabled:opacity-70"
          >
            {FLOORS_ARRAY.map((_, i) => (
              <option key={i} value={i + 1}>
                {i + 1} Floors
              </option>
            ))}
          </select>
          <span className="text-xs text-gray-400">
            Total number of floors to carry up or down in both pick and dropoff
            locations
          </span>
        </div>
        <div>
          <select
            {...register("payment_option")}
            id="payment_option"
            name="payment_option"
            disabled={disableUserInterface}
            className="mt-4 block text-sm appearance-none w-full border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded-lg leading-tight focus:outline-none focus:ring-yellow-400 focus:bg-white focus:border-yellow-400  disabled:bg-gray-200 disabled:pointer-events-none disabled:opacity-70"
          >
            {["EFT", "CASH"].map((paymentOption, i) => (
              <option key={i} value={paymentOption}>
                {paymentOption} payment
              </option>
            ))}
          </select>
          <span className="text-xs text-gray-400">
            Choose your prefared payment option
          </span>
        </div>
        <div className="mt-4">
          <textarea
            {...register("customer_driver_note")}
            type="text"
            rows={3}
            name="customer_driver_note"
            placeholder="Leave a note for the driver"
            disabled={disableUserInterface}
            className={cn(
              "text-sm text-gray-700 w-full rounded-lg border-gray-200 focus:bg-white focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-50 disabled:pointer-events-none  disabled:bg-gray-200"
            )}
          />
          <span className="text-xs text-gray-400">
            Need to give the driver additional instruction?, leave a note for
            the driver here.
          </span>
        </div>

        {/* <button type="submit">Click me</button> */}
        <div className="py-4">
          {isUpdateError && (
            <ErrorAlert
              className="mb-4"
              secondaryText={`${updateError.response.data.message} ${updateError.message}`}
            />
          )}
          {isUpdateSucces && (
            <SuccessAlert
              secondaryText={`Additional information successfully updated.`}
              className="mb-4 w-full"
            />
          )}
          <div className="flex flex-row items-center justify-end gap-3">
            {/*alert*/}

            <Button disabled={disableUserInterface} className="w-auto">
              {isUpdateLoading ? (
                <div className="w-full h-full flex justify-center items-center">
                  <ThreeDots visible width="40" height="20" color="#000" />
                </div>
              ) : (
                "Update"
              )}
            </Button>
          </div>
        </div>
      </form>
    </div>
  );
}
