import { useParams } from "react-router";
import { UpdateAdditionalINformationCard } from "../reusable_components/update_additional_information_card";

export default function UpdateAdditionalInformation() {
  const { id } = useParams();
  return (
    <main className="max-w-xl mx-auto p-6 my-6 text-black bg-white">
      <div className="rounded-lg p-6 shadow-sm border border-gray-200">
        <h2 className="text-start text-black px-2 font-extrabold text-3xl">
          Update Additional Information
          <p className="text-sm font-normal text-slate-600 pt-2">
            Trust matols logistics services to transport your items safely.
          </p>
          <p className="text-sm font-bold pt-2">Booking {id}</p>
        </h2>

        <UpdateAdditionalINformationCard />
      </div>
    </main>
  );
}
